.container {
    width: 100%;
    min-height: 100%;
}

.headingSection {
    width: 100%;
    height: auto;
    padding: 30px 0;
    display: relative;
}

.laptopIcon {
    font-size: 45px;
}

.headingText {
    font-size: 45px;
    font-weight: 900;
    composes: headingText from '../../../../CommonUI/colors.css';
    word-spacing: 0px;
    line-height: 50px;
    /* font-family: 'Roboto Condensed'; */

}

@media (max-width: 1120px) {
    .headingText{ 
        font-size: 35px;
    }
    .laptopIcon {
        font-size: 35px;
    }
}
@media (max-width: 457px) {
    .headingText{ 
        font-size: 25px;
    }
    .laptopIcon {
        font-size: 25px;
    }
} 
