.container {
    position: relative;
    width: 100%;
    height: 80px;
    /* position: relative; */
}

.horizontalSection {
    width: 100%;
    height: 100%;
    display: flex;
    align-content: center;
    justify-content: space-between;
    flex-direction: row;
    /* composes: boxShadow from '../../colors.css'; */
    composes: boxShadow from '../../colors.css';
}

.logo {
    padding-left: 30px;
    width: 8%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.resizeImage {
    width: 90px;
    height: 86px;
}

.rightEnd {
    width: 85%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}

.menu {
    width: 40%;
    margin-right: 100px;
    padding: 14px 14px;
    /* border: 1px solid black; */
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.menuItem {
    /* border: 1px solid black; */
    padding: 4px 16px;
    cursor: pointer;
    font-weight: 500;
    composes: buttonRadiusSmall from '../../colors.css';
    composes: subText from '../../colors.css';
    composes: hoverColor from '../../colors.css';
    transition: 0.3s;
}

.mobileMenu {
    composes: menu;
    margin-right: 0;
    width: 10%;
}

.icon {
    cursor: pointer;
    composes: iconConfig from '../../icons.css';
}

.menuIcon {
    composes: menuIcon from '../../icons.css';
}

.dropMenu {
    position: absolute;
    background-color: white;
    top: -500px;
    right: 0;
    width: 100%;
    height: 150px;
    composes: boxShadowMenu from '../../colors.css';
    -webkit-transition: all 0.7s ease;  
    -moz-transition: all 0.7s ease;  
    -o-transition: all 0.7s ease;  
    -ms-transition: all 0.7s ease;  
    transition: all 0.7s ease;
}

.dropMenuActive {
    top: 80px;
}

.menuItemDropdown {
    padding: 10px 16px;
    cursor: pointer;
    font-weight: 500;
    composes: buttonRadiusSmall from '../../colors.css';
    composes: subText from '../../colors.css';
    composes: hoverColor from '../../colors.css';
    transition: 0.3s;
}